import request from '@/utils/request'

// 获取收获地址列表
export const getAddressList = () => {
  return request.get('/address/list')
}
//  获取收获地址详情
export const getAddressDetail = (addressId) => {
  return request.get('/address/detail', {
    params: {
      addressId
    }
  })
}

// 获取默认收获地址id
export const getDefaultAddress = () => {
  return request.get('/address/defaultId')
}
// 设置默认地址
export const setDefaultAddress = (addressId) => {
  return request.post('/address/setDefault', {
    addressId
  })
}

// 获取省市区-用于添加地址
export const getRegionAddress = () => {
  return request.get('/region/tree')
}
// 添加收获地址 region{value,label}
export const addAddress = (obj) => {
  const { name, phone, region, detail } = obj
  return request.post('/address/add', {
    form: {
      name,
      phone,
      region,
      detail
    }
  })
}

// 删除收获地址
export const delAddress = (addressId) => {
  return request.post('/address/remove', {
    addressId
  })
}

// 编辑收获地址
export const editAddress = (addressId, obj) => {
  const { name, phone, region, detail } = obj
  return request.post('/address/edit', {
    addressId,
    form: {
      name,
      phone,
      region,
      detail
    }
  })
}
