import { getInfo, setInfo, removeInfo, removeHistory } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getInfo()
    }
  },
  mutations: {
    setUser (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      context.commit('setUser', {})
      context.commit('cart/setCartList', { list: [], cartTotal: 0 }, { root: true })
      context.commit('address/setAddressList', [], { root: true })
      context.commit('address/getDefaultAdds', 0, { root: true })

      removeInfo()
      removeHistory()

      setTimeout(() => {
        location.reload()
      }, 100)
    }
  },
  getters: {}
}
