import {
  getAddressList,
  addAddress,
  setDefaultAddress,
  getDefaultAddress,
  delAddress,
  editAddress
} from '@/api/address'

export default {
  namespaced: true,
  state: {
    addressList: [], // 用户所有地址
    defaultId: 0
  },
  mutations: {
    setAddressList (state, list) {
      state.addressList = list
    },
    getDefaultAdds (state, defaultId) {
      state.defaultId = defaultId
    }
  },
  actions: {
    // 获取地址列表
    async getAddressList (context) {
      const { data: { list } } = await getAddressList()
      context.commit('setAddressList', list)
    },

    // 添加地址 obj = { name, phone, region[{},{},{}], detail }
    async addNewAddress (context, obj) {
      await addAddress(obj)
      context.dispatch('getAddressList')
    },

    // 获取默认地址id
    async getDefaultAdds (context) {
      const { data: { defaultId } } = await getDefaultAddress()
      context.commit('getDefaultAdds', defaultId)
    },

    // 设置默认地址
    async setDefaultAdds (context, addressId) {
      await setDefaultAddress(addressId)
      context.commit('getDefaultAdds', addressId)
    },

    // 删除地址
    async delAdds (context, addressId) {
      await delAddress(addressId)
      context.dispatch('getAddressList')
    },

    // 编辑地址
    async editAdds (context, obj) {
      const { addressId, newadds } = obj
      await editAddress(addressId, newadds)
      context.dispatch('getAddressList')
    }

  }
}
